import React, {useState} from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </Typography>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
  },
}));

export default function SimpleTabs() {
  const classes = useStyles();
  const [value, setValue] = useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div className={classes.root}>
      <AppBar position="static">
        <Tabs value={value} onChange={handleChange} aria-label="resume tabs">
          <Tab label="Web Developer" {...a11yProps(0)} />
          <Tab label="Network Administrator" {...a11yProps(1)} />
          <Tab label="IT Professional" {...a11yProps(2)} />
          <Tab label="System Administrator" {...a11yProps(3)} />
          <Tab label="General Purpose Programmer" {...a11yProps(4)} />
          <Tab label="Database Administrator" {...a11yProps(5)} />
          <Tab label="System Architect" {...a11yProps(6)} />
          <Tab label="Asset Creator" {...a11yProps(7)} />
        </Tabs>
      </AppBar>
      <TabPanel value={value} index={0}>
        Tailored resume for Web Development goes here
      </TabPanel>
      <TabPanel value={value} index={1}>
        Tailored resume for network admin goes here
      </TabPanel>
      <TabPanel value={value} index={2}>
        Tailored resume for IT Professional goes here
      </TabPanel>
      <TabPanel value={value} index={3}>
        Tailored resume for IT Professional goes here
      </TabPanel>
      <TabPanel value={value} index={4}>
        Tailored resume for General Programmer goes here
      </TabPanel>
      <TabPanel value={value} index={5}>
        Tailored resume for Database Admin goes here
      </TabPanel>
      <TabPanel value={value} index={6}>
        Tailored resume for System Architect (pc/server/machine builder/designer) goes here
      </TabPanel>
      <TabPanel value={value} index={7}>
        Tailored resume for Asset Creator goes here
      </TabPanel>
    </div>
  );
}
