import React from "react";
import { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
// import CardActionArea from "@material-ui/core/CardActionArea";
// import CardActions from '@material-ui/core/CardActions';
import CardContent from "@material-ui/core/CardContent";
// import CardMedia from "@material-ui/core/CardMedia";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import Typography from "@material-ui/core/Typography";
import Input from "@material-ui/core/Input";

import Grid from "@material-ui/core/Grid";

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1
  }
}));

export default function Bulletin() {
  const classes = useStyles();
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [verifyPassword, setVerifyPassword] = useState("");
  const [email, setEmail] = useState("");
  const [salt, setSalt] = useState("");
  const [statusText, setStatusText] = useState("");
  //   const [hashedSaltedPassword, setHashedSaltedPassword] = useState("");

  //   const hash_password = () => {
  //     let salted_password = salt + password;
  //     //let hashed_password = hash(salted_password);
  //     let hashed_password = "butts";
  //     setHashedSaltedPassword(hashed_password);
  //   };

  const submitRegistration = () => {
    setStatusText("Submitting...");
    console.log("submitting...");
    console.log(username);
    console.log(email);
    console.log(password);
    console.log(verifyPassword);
    //Submit button should be disabled if password !== verifyPassword, but let's check anyway
    if (password === verifyPassword) {
      let hashedSaltedPassword = "buttz";

      let data = {
        username: username,
        email: email,
        salt: salt,
        hashedsaltedpassword: hashedSaltedPassword
      };
      fetch("https://api.rudyharrelson.com/db/create/user", {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify(data) // body data type must match "Content-Type" header
      }).then(async res => {
        //if status isn't 200, registration failed
        if (res.status === 200) {
          setStatusText("Registration successful!");
        } else {
          setStatusText("Registration failed: " + (await res.text()));
        }
      });
    }
  };

  return (
    <Grid container className={classes.root} style={{ display: "flex", flexDirection: "column", justifyContent: "flex-start", alignItems: "center" }}>
      <Grid
        item
        xs={10}
        md={8}
        lg={4}
        style={{ width: "100%", display: "flex", flexDirection: "column", justifyContent: "flex-start", alignItems: "center", marginBottom: "20px" }}
      >
        <Input
          autoFocus
          onChange={e => {
            setUsername(e.target.value);
          }}
          placeholder={"Username"}
          value={username || ""}
          style={{ marginBottom: "20px" }}
        />
        <Input
          autoFocus
          onChange={e => {
            setEmail(e.target.value);
          }}
          placeholder={"Email"}
          value={email || ""}
          style={{ marginBottom: "20px" }}
        />
        <Input
          autoFocus
          onChange={e => {
            setPassword(e.target.value);
          }}
          type={"password"}
          placeholder={"Password"}
          value={password || ""}
          style={{ marginBottom: "20px" }}
        />
        <Input
          autoFocus
          onChange={e => {
            setVerifyPassword(e.target.value);
          }}
          type={"password"}
          placeholder={"Verify Password"}
          value={verifyPassword || ""}
          style={{ marginBottom: "20px" }}
        />
        <br />
        <Button
          variant="contained"
          color="primary"
          size="large"
          style={{ marginBottom: "20px" }}
          onClick={submitRegistration}
          disabled={!username || !email || !password || password !== verifyPassword}
        >
          Register
        </Button>
        <br />
        <br />
        <div style={{ fontWeight: "bold" }}>{statusText}</div>
      </Grid>
    </Grid>
  );
}
